export default {
    state: {
        title:'MY STUDY WEB',
        page1:true,
        page2:false,
        userPage:true
    },
    mutations: {
        changePage(state){
            let temp;
            temp = state.page1;
            state.page2 = temp;
            state.page1 = !state.page2;
        },
        toModifyPage(state){
            state.userPage = !state.userPage;
        }
    }
}